import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { IStatus } from '@shared/models';

export enum HISTORY_FILTER_STATUS {
  ALL = 'ALL',
  FOR_SIGN = 'FOR_SIGN',
  COMPLETED = 'COMPLETED',
  REJECTED = 'REJECTED',
  IN_PROGRESS = 'IN_PROGRESS',
  DRAFTS = 'DRAFTS',
}

@Component({
  selector: 'j-history-filter',
  templateUrl: './history-filter.component.html',
  styleUrls: ['./history-filter.component.scss'],
})
export class HistoryFilterComponent {
  @Output() filtered = new EventEmitter();

  statuses: IStatus[] = [
    {
      label: 'HISTORY.LISTS.ALL',
      value: HISTORY_FILTER_STATUS.ALL,
      count: 0,
      isSelected: true,
    },
    {
      label: 'HISTORY.LISTS.FOR_SIGN',
      value: HISTORY_FILTER_STATUS.FOR_SIGN,
      count: 0,
    },
    {
      label: 'HISTORY.LISTS.SUCCESS',
      value: HISTORY_FILTER_STATUS.COMPLETED,
      count: 0,
    },
    {
      label: 'HISTORY.LISTS.FAILED',
      value: HISTORY_FILTER_STATUS.REJECTED,
      count: 0,
    },
    {
      label: 'HISTORY.LISTS.IN_PROGRESS',
      value: HISTORY_FILTER_STATUS.IN_PROGRESS,
      count: 0,
    },
    {
      label: 'HISTORY.LISTS.DRAFT',
      value: HISTORY_FILTER_STATUS.DRAFTS,
      count: 0,
    },
  ];

  constructor() {}

  onStatusChange(status: HISTORY_FILTER_STATUS) {
    this.filtered.emit({ status });
  }
}
