import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { History2Component } from './history2.component';
import { PAYMENT_CATEGORY } from '@shared/models';
import { History2ListComponent } from './containers';

const routes: Routes = [
  {
    path: '',
    component: History2Component,
    data: {
      title: 'HISTORY.TITLE',
    },
    children: [
      {
        path: 'all',
        component: History2ListComponent,
        data: {
          category: PAYMENT_CATEGORY.ALL,
        },
      },
      {
        path: 'tenge',
        component: History2ListComponent,
        data: {
          category: PAYMENT_CATEGORY.DOMESTIC,
        },
      },
      {
        path: 'foreign',
        component: History2ListComponent,
        data: {
          category: PAYMENT_CATEGORY.FOREIGN,
        },
      },
      {
        path: 'conversion',
        component: History2ListComponent,
        data: {
          category: PAYMENT_CATEGORY.CONVERSION,
        },
      },
      {
        path: '**',
        redirectTo: 'all',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class History2RoutingModule {}
